   picImg: "",
   format: "",
   window: {
    width: 0,
    height: 0,
   },
  };
 },
 methods: {
  mediaFormat(formatPic) {
   this.format = formatPic;
  },
  backgroundPNG(updatedPicture) {
   this.picImg = require("../assets/" + updatedPicture + "." + this.format);
  },
  resizer() {
   this.window.width = window.innerWidth;
   this.window.height = window.innerHeight;
  },
  winAlert() {
   if (this.window.width < 775) {
    console.log("alerted");
   }
  },
 },
 created() {
  window.addEventListener("resize", this.resizer);
  this.resizer();
  this.winAlert();
 },
 destroyed() {
  window.removeEventListener("resize", this.resizer);
 },
};
</script>

