import Vue from 'vue'
import router from './router'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faAngleRight, faAngleLeft, faMinus } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faFontAwesome, faLinkedin } from '@fortawesome/free-brands-svg-icons'
library.add(faAngleRight, faAngleLeft, faMinus, faFontAwesome, faLinkedin)
Vue.component('font-awesome-icon', FontAwesomeIcon)

//code
import App from './App.vue'

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')


