<template>
 <div>
  <div @keyup.right="nextPage"></div>
  <img style="display:none;" :src="backgroundPNG" alt="image" />

  <div class="pageSelectors">
   <font-awesome-icon icon="angle-left" size="4x" class="arrowDown" @click="backPage" />
   <font-awesome-icon icon="minus" size="2x" class="minus" @click="nextPage" />
   <font-awesome-icon icon="angle-right" size="4x" class="arrowUp" @click="nextPage" />
  </div>

  <div class="accentBGtext scroll-left">
   <p>{{projectsDataIn[currentPage].title}}</p>
  </div>

  <transition
   appear
   mode="out-in"
   enter-active-class="animate__animated animate__fadeIn"
   leave-active-class="animate__animated animate__fadeOut"
  >
   <div class="tabBGtext">Projects:</div>
  </transition>

  <transition
   appear
   mode="out-in"
   enter-active-class="animate__animated animate__fadeIn"
   leave-active-class="animate__animated animate__fadeOut"
  >
   <div :key="currentPage" class="primaryBGtext">{{projectsDataIn[currentPage].title}}</div>
  </transition>

  <div class="descriptionArea">
   <transition
    appear
    mode="out-in"
    enter-active-class="animate__animated animate__fadeIn"
    leave-active-class="animate__animated animate__fadeOut"
   >
    <div :key="currentPage">{{projectsDataIn[currentPage].description}}</div>
   </transition>
  </div>
 </div>
</template>

<script>
export default {
 data() {
  return {
   currentPage: 0,
  };
 },
 props: {
  projectsDataIn: Array,
 },
 methods: {
  timeReset: function () {
   clearInterval(this.timer);
   this.timer = setInterval(this.nextPage, 18000);
  },
  nextPage: function () {
   if (this.currentPage == this.$props.projectsDataIn.length - 1) {
    this.timeReset();
    return (this.currentPage = 0);
   }
   this.timeReset();
   return (this.currentPage += 1);
  },
  backPage: function () {
   if (this.currentPage == 0) {
    this.timeReset();
    return (this.currentPage = this.$props.projectsDataIn.length - 1);
   }
   this.timeReset();
   return (this.currentPage -= 1);
  },
 },
 computed: {
  backgroundPNG() {
   this.$emit("mediaFormat", this.projectsDataIn[this.currentPage].mediaFormat);
   this.$emit("updateBGpic", this.projectsDataIn[this.currentPage].picture);
   if (this.projectsDataIn[this.currentPage].mediaFormat == "png") {
    return require("../assets/" +
     this.projectsDataIn[this.currentPage].picture +
     ".png");
   } else {
    return require("../assets/" +
     this.projectsDataIn[this.currentPage].picture +
     ".gif");
   }
  },
 },
 mounted() {
  window.addEventListener("keyup", (event) => {
   if (event.keyCode === 39) {
    this.nextPage();
   } else if (event.keyCode === 32) {
    this.nextPage();
   }
   if (event.keyCode === 37) {
    this.backPage();
   }
  });
 },
 created() {
  this.timer = setInterval(this.nextPage, 18000);
 },
 beforeDestroy() {
  clearInterval(this.timer);
 },
};
</script>

<style >
.animate__animated.animate__fadeIn {
 --animate-duration: 2s;
}
.scroll-left {
 overflow: hidden;
 position: relative;
 height: 40vh;
 width: 50vw;
 left: 0px;
 top: 20vh;
 z-index: -1;
}
.scroll-left p {
 position: absolute;
 line-height: 20vh;
 text-align: center;
 /* Starting position */
 -moz-transform: translateX(100%);
 -webkit-transform: translateX(100%);
 transform: translateX(100%);
 /* Apply animation to this element */
 -moz-animation: scroll-left 12s linear infinite;
 -webkit-animation: scroll-left 12s linear infinite;
 animation: scroll-left 12s linear infinite;
}
/* Move it (define the animation) */
@-moz-keyframes scroll-left {
 0% {
  -moz-transform: translateX(100%);
 }
 100% {
  -moz-transform: translateX(-100%);
 }
}
@-webkit-keyframes scroll-left {
 0% {
  -webkit-transform: translateX(100%);
 }
 100% {
  -webkit-transform: translateX(-100%);
 }
}
@keyframes scroll-left {
 0% {
  -moz-transform: translateX(100%); /* Browser bug fix */
  -webkit-transform: translateX(100%); /* Browser bug fix */
  transform: translateX(100%);
 }
 100% {
  -moz-transform: translateX(-100%); /* Browser bug fix */
  -webkit-transform: translateX(-100%); /* Browser bug fix */
  transform: translateX(-100%);
 }
}
</style>


