<template>
 <div>
  <div @keyup.right="nextPage"></div>
  <img style="display:none;" :src="backgroundPNG" alt="image" />

  <div class="accentBGtext scroll-left">
   <p>Get in touch!</p>
  </div>

  <transition
   appear
   mode="out-in"
   enter-active-class="animate__animated animate__fadeIn"
   leave-active-class="animate__animated animate__fadeOut"
  >
   <div class="tabBGtext">Get in touch!</div>
  </transition>

  <transition
   appear
   mode="out-in"
   enter-active-class="animate__animated animate__fadeIn"
   leave-active-class="animate__animated animate__fadeOut"
  >
   <div :key="currentPage" class="primaryBGtext">{{contactDataIn[currentPage].title}}</div>
  </transition>

  <div class="descriptionArea">
   <transition
    appear
    mode="out-in"
    enter-active-class="animate__animated animate__fadeIn"
    leave-active-class="animate__animated animate__fadeOut"
   >
    <div :key="currentPage">{{contactDataIn[currentPage].description}}</div>
   </transition>
  </div>
 </div>
</template>

<script>
export default {
 data() {
  return {
   currentPage: 0,
  };
 },
 props: {
  contactDataIn: Array,
 },
 methods: {
  timeReset: function () {
   clearInterval(this.timer);
   this.timer = setInterval(this.nextPage, 18000);
  },
  nextPage: function () {
   if (this.currentPage == this.$props.contactDataIn.length - 1) {
    this.timeReset();
    return (this.currentPage = 0);
   }
   this.timeReset();
   return (this.currentPage += 1);
  },
  backPage: function () {
   if (this.currentPage == 0) {
    this.timeReset();
    return (this.currentPage = this.$props.contactDataIn.length - 1);
   }
   this.timeReset();
   return (this.currentPage -= 1);
  },
 },
 computed: {
  backgroundPNG() {
   this.$emit("mediaFormat", this.contactDataIn[this.currentPage].mediaFormat);
   this.$emit("updateBGpic", this.contactDataIn[this.currentPage].picture);
   if (this.contactDataIn[this.currentPage].mediaFormat == "png") {
    return require("../assets/" +
     this.contactDataIn[this.currentPage].picture +
     ".png");
   } else {
    return require("../assets/" +
     this.contactDataIn[this.currentPage].picture +
     ".gif");
   }
  },
 },
 mounted() {
  window.addEventListener("keyup", (event) => {
   if (event.keyCode === 39) {
    this.nextPage();
   } else if (event.keyCode === 32) {
    this.nextPage();
   }
   if (event.keyCode === 37) {
    this.backPage();
   }
  });
 },
 created() {
  this.timer = setInterval(this.nextPage, 18000);
 },
 beforeDestroy() {
  clearInterval(this.timer);
 },
};
</script>